      "\u003cp\u003e\u003cstrong\u003eAbout Sticker Mule\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eSticker Mule is the Internet's fastest-growing printing company. We are privately-owned, profitable, and powered by an outstanding, globally distributed team who cares deeply about delivering a great customer experience at the highest technical standards. Our software engineering team operates from 17 different countries, and we're always looking for more exceptional engineers to join us.\u003c/p\u003e\n\u003cp\u003eThe Software team is responsible for building and maintaining our front-end and back-end services, developing new features and products, and troubleshooting issues in collaboration with the Support team.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003e\u003ca href=\"https://www.stickermule.com/about\"\u003eSee more about our teams here\u003c/a\u003e\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eWhat we offer\u003c/strong\u003e\u003c/p\u003e\n\u003col\u003e\n\u003cli\u003eRemote working with flexible schedule.\u003c/li\u003e\n\u003cli\u003eOpportunity to make a large impact as part of a small, highly motivated team.\u003c/li\u003e\n\u003cli\u003eVaried, interesting technical challenges to work on.\u003c/li\u003e\n\u003c/ol\u003e\n\u003cp\u003e\u003cstrong\u003eRequirements\u003c/strong\u003e\u003c/p\u003e\n\u003col\u003e\n\u003cli\u003eDocker\u003c/li\u003e\n\u003cli\u003eNextJS\u003c/li\u003e\n\u003cli\u003eReact\u003c/li\u003e\n\u003cli\u003eNodeJS\u003c/li\u003e\n\u003cli\u003eGraphQL\u003c/li\u003e\n\u003cli\u003ePostgres\u003c/li\u003e\n\u003cli\u003eRedis\u003c/li\u003e\n\u003cli\u003eFamiliarity with Ruby\u003c/li\u003e\n\u003cli\u003eExcellent communication skills (English)\u003c/li\u003e\n\u003cli\u003eDegree in Computer Science or equivalent practical experience\u003c/li\u003e\n\u003c/ol\u003e\n\u003cp\u003e\u003cstrong\u003eThe process\u003c/strong\u003e\u003c/p\u003e\n\u003col\u003e\n\u003cli\u003eSelected candidates are invited for an introductory interview\u003c/li\u003e\n\u003cli\u003eA technical interview discussing your background and work samples provided\u003c/li\u003e\n\u003cli\u003eA final meeting with our VP of HR.\u003c/li\u003e\n\u003c/ol\u003e\n\u003cp\u003e\u003cstrong\u003eCompensation and benefits\u003c/strong\u003e\u003c/p\u003e\n\u003col\u003e\n\u003cli\u003e$90,000-$115,000+\u003c/li\u003e\n\u003cli\u003eSigning bonus\u003c/li\u003e\n\u003cli\u003e4 weeks vacation\u003c/li\u003e\n\u003c/ol\u003e\n",
