    location: "Remote (USA)",
    title: "JavaScript Application Engineer (Remote)",
    description:
      "\u003cp\u003eWe're looking for a JavaScript Application Engineer to help us build out our SaaS Learning Business Platform. Your day to day would be split between improving existing functionality, building \u0026amp; shipping new functionality \u0026amp; integrations, and improving the platform as a whole, such as introducing bundle splitting, working on performance, and upgrading our dependencies.\u003c/p\u003e\n\u003cp\u003eYou would be a good fit if:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eYou are excited about new JavaScript technologies like Prettier and GraphQL, but also comfortable mixing them with older technology.\u003c/li\u003e\n\u003cli\u003eYou sweat the small stuff, but know when to ship it.\u003c/li\u003e\n\u003cli\u003eYou are passionate about listening to users \u0026amp; building elegant web products.\u003c/li\u003e\n\u003cli\u003eYou are comfortable navigating a large existing codebase and learning new technologies and techniques along the way.\u003c/li\u003e\n\u003cli\u003eYou have 2+ years of experience working in a team production environment shipping code.\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eWe are looking to add multiple team members across frontend and backend roles, so we are open to your experience across the stack.\u003c/p\u003e\n\u003cp\u003eOur stack consists primarily of Node.js with Koa, GraphQL, and Ember.js. Although we are an Ember shop, we are open to any framework experience you might have as the concepts will likely be familiar.\u003c/p\u003e\n\u003cp\u003eThroughout the day, we rely on Slack and GitHub pull requests. The development team is completely distributed across the US, and has been since the inception of Thought Industries — we haven't bolted on remote processes due to COVID-19, we've always done it this way, so you'll feel right at home.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eAbout Thought Industries\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eThought Industries is a startup in the Online Learning space. We enable training and software companies to launch and monetize external learning programs — think Shopify meets Udemy/Coursera.\u003c/p\u003e\n\u003cp\u003eThis is a full-time position that is based at your remote U.S. based office. No Recruiters, please.\u003c/p\u003e\n\u003cp\u003eHeadquartered in Boston, Massachusetts, Thought Industries is one of the world’s fastest-growing online learning companies in the U.S. We are helping consumer brands and for-profit learning organizations change how they build, deploy and grow online learning businesses. Today, hundreds of customers and brands are using the Thought Industries’ Learning Business Platform to transform the way they reach, teach, and engage audiences.\u003c/p\u003e\n\u003cp\u003eWe are a growing, well-funded technology company, with a talented team and a clear vision. This is a unique opportunity to take a lead role at an exciting SaaS software company with a robust cloud-based platform. We hire talented people who are self-motivated and team orientated. All qualified applicants will receive consideration for employment without regard to race, color, religion, sex, sexual orientation, gender identity, national origin, age, disability or veteran status.\u003c/p\u003e\n",
