    description:
      '\u003cp\u003e\u003cstrong\u003eTHRIVE WITH AGILOFT\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eAre you an experienced Sr. Front End Developer who is successful, motivated, smart, energetic, and looking for a rewarding position in a growing, profitable and dynamic company?\u003c/p\u003e\n\u003cp\u003eAgiloft is expanding our developer team, looking for Sr. Front End Developers that thrive working with enterprises in multiple industries, with unique business challenges requiring sophisticated solution design.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eWHY JOIN AGILOFT?\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003e\n\u003cp\u003eAgiloft was named the market leader in every category in the \u003ca href="https://www.agiloft.com/flash/Gartner_critical_capabilities_for_CLM.pdf"\u003eGartner Critical Capabilities for CLM report\u003c/a\u003e, and a market leader in the Gartner Magic Quadrant.\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eContract Lifecycle Management (CLM) is one of the fastest growing areas of enterprise sales, with a TAM projected to climb from $2B to $7B in the next 5 years.\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eThe Agiloft Contract and Commerce Lifecycle Platform has won over \u003ca href="https://www.agiloft.com/awards.htm"\u003ea dozen awards, including the Editor\'s Choice award from PC Mag\u003c/a\u003e, for the past five years in a row.\u003c/p\u003e\n\u003c/li\u003e\n\u003c/ul\u003e\n\u003cul\u003e\n\u003cli\u003e\n\u003cp\u003eAgiloft has a highly differentiated value proposition which is uniquely appealing to enterprises: pre-built applications with a deeply configurable, no-code platform for integrated Business Process Management throughout an organization.\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eAgiloft is pioneering the applied use of Artificial Intelligence to enable next-generation business commerce at organizations ranging from small enterprises to U.S. government agencies and Fortune 100 companies.\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eAgiloft is the only company in the industry with the confidence to provide an unconditional satisfaction guarantee, and you too will be successful with Agiloft.\u003c/p\u003e\n\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eAs the leader in the CCLM market, Agiloft is winning many Enterprise deals and looking to hire the best talent to meet this demand with a competitive compensation plan that rewards overachievement.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003ePOSITION OVERVIEW\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eThe Senior Front-End Developer will join the development team responsible for implementation, improvement and maintenance of the Agiloft frontend architecture. The ideal candidate will have demonstrated experience with the development and maintenance of Enterprise-grade applications as well as the application of modern frontend frameworks.\u003c/p\u003e\n\u003cp\u003eThis is a remote-only position. You must be comfortable with setting your own schedule and proactively collaborating in a globally dispersed team using a variety of communication channels.\u003c/p\u003e\n\u003cp\u003e\u003cstrong\u003eJOB RESPONSIBILITIES\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eDesign, build, and maintain efficient, reusable, and reliable front-end code.\u003c/li\u003e\n\u003cli\u003eBuild cross-browser solutions optimized for a wide range of devices.\u003c/li\u003e\n\u003cli\u003eIdentify UI problems and bugs and devise elegant solutions.\u003c/li\u003e\n\u003cli\u003eMake strategic technical decisions related to new products and functionalities.\u003c/li\u003e\n\u003cli\u003eHelp maintain code quality.\u003c/li\u003e\n\u003cli\u003eOnboard and mentor new team members.\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eREQUIRED QUALIFICATIONS\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003e\n\u003cp\u003eA minimum of three years of experience in working on large projects/Enterprise applications including, but not limited to version control tools, automated software testing, automating software build tools\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eStrong knowledge of HTML5, CSS3, JavaScript and modern frontend development frameworks\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eKnowledgeable about UI development concepts like Responsive Design, SPA, Modular Design\u003c/p\u003e\n\u003c/li\u003e\n\u003cli\u003e\n\u003cp\u003eWorking knowledge of Java\u003c/p\u003e\n\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003eExperience with:\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eStruts 2, JSP\u003c/li\u003e\n\u003cli\u003ePopular JS frameworks like Vue, React, Angular\u003c/li\u003e\n\u003cli\u003eRelational databases such as MySQL, MSSQL, Oracle\u003c/li\u003e\n\u003cli\u003eRest API implementation\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eBENEFITS AND PERKS\u003c/strong\u003e\u003c/p\u003e\n\u003cul\u003e\n\u003cli\u003eCompetitive salary ($30 USD hourly)\u003c/li\u003e\n\u003cli\u003eReferral bonuses\u003c/li\u003e\n\u003cli\u003eFlexible work schedules\u003c/li\u003e\n\u003cli\u003eProfessional development and career growth opportunities\u003c/li\u003e\n\u003cli\u003eAwesome team members\u003c/li\u003e\n\u003c/ul\u003e\n\u003cp\u003e\u003cstrong\u003eINTERESTED? APPLY NOW!\u003c/strong\u003e\u003c/p\u003e\n\u003cp\u003eFor a full list of open positions and to submit your application, go to our website \u003ca href="http://www.agiloft.com/jobs.htm"\u003ehttp://www.agiloft.com/jobs.htm\u003c/a\u003e\u003c/p\u003e\n',
